/* eslint-disable */
import $ from 'jquery';
import Swiper from 'swiper';

window.$ = $;
window.jQuery = $;

// start slider
$(() => {
    new Swiper(".start .swiper-container", {
        pagination: {
          el: ".start .swiper-pagination",
          clickable: true
        },
        spaceBetween: 30
    });
});

// services slider
$(() => {
    const swiper = new Swiper(".services .swiper-container", {
        pagination: {
          el: ".services .swiper-pagination",
          type: "fraction",
        },
        navigation: {
          nextEl: ".services .swiper-button-next",
          prevEl: ".services .swiper-button-prev",
        },
        on: {
            init: function() {
                const $slide = $(this.slides[this.activeIndex]);
                const id = $slide.attr('id');
                const data = $slide.data('dataslide');

                $(`.services .menu ul a`).removeClass('active')
                $(`.services .menu ul a[href="#${id}"]`).addClass('active');

                for(const key in data) {
                    if (key.indexOf('photo') >= 0) {
                        $(`#${key}`).css({ backgroundImage: `url('${data[key]}')` });
                    } else {
                        $(`#${key} img`).attr('src', data[key].icon);
                        $(`#${key} p`).html(data[key].text);
                    }
                }
            },
            slideChange: function() {
                const $slide = $(this.slides[this.activeIndex]);
                const id = $slide.attr('id');
                const data = $slide.data('dataslide');

                $(`.services .menu ul a`).removeClass('active')
                $(`.services .menu ul a[href="#${id}"]`).addClass('active');
            
                for(const key in data) {
                    if (key.indexOf('photo') >= 0) {
                        $(`#${key}`).css({ backgroundImage: `url('${data[key]}')` });
                    } else {
                        $(`#${key} img`).attr('src', data[key].icon);
                        $(`#${key} p`).html(data[key].text);
                    }
                }
            }
        }
    });

    $(`.services .menu ul a`).on('click', function(e) {
        e.preventDefault();

        const slideID = $(this).attr('href');
        let slideIndex = null;

        $(swiper.slides).each((i, slide) => {
            if (`#${$(slide).attr('id')}` === slideID) {
                slideIndex = i;
            }
        });

        swiper.slideTo(slideIndex);
    });
});

// partners slider
$(() => {
    const swiper = new Swiper(".partners .swiper-container", {
        navigation: {
          nextEl: ".partners .swiper-button-next",
          prevEl: ".partners .swiper-button-prev",
        },
        slidesPerView: 6,
        loop: true,
        breakpoints: {
            320: {
                slidesPerView: 3,
                slidesPerColumn: 3,
                spaceBetween: 10
            },
            991: {
                slidesPerView: 6,
                slidesPerColumn: 1,
                spaceBetween: 0
            }
        }
    });
});

// dropdown hover
$(() => {
    let timeoutID = null;
    let ms = 300;
    $(".dropdown-menu > li > a").mouseenter(function() {
        if($(this).next('ul').length) {
            clearTimeout(timeoutID);
            $(this).next('ul').show();
        }
    });
    
    $(".dropdown-menu > li > a").mouseleave(function() {
        if($(this).next('ul').length) {
            timeoutID = setTimeout(() => {
                $(this).next('ul').hide();
            }, ms);
        }
    });
    $(".dropdown-menu > li > ul").mouseenter(function() {
        clearTimeout(timeoutID);
        $(this).show();
    });
    
    $(".dropdown-menu > li > ul").mouseleave(function() {
        timeoutID = setTimeout(() => {
            $(this).hide();
        }, ms);
    });
});

// up
$(() => {
    const btn = $('#up');
    $(window).scroll(function() {
        if ($(window).scrollTop() >= $(document).height() - (window.innerHeight + $('.footer').height())) {
            btn.addClass('show');
        } else {
            btn.removeClass('show');
        }
    });
    
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({ scrollTop: 0 }, 1000);
    });
});

// scroll to
$(() => {
    $("[scrollto]").click(function (e) {
        e.preventDefault();

        const id = $(this).attr('scrollto');

        $('html, body').animate({
            scrollTop: $(id).offset().top
        }, 1000);
    });
});

// project tabs
$(() => {
    $('.projects__items').hide();
    $('#projAll').show();

    $('.projects__menu button').on('click', function() {
        const tabID = $(this).data('tab');

        $('.projects__menu button').removeClass('active');
        $(this).addClass('active');

        $('.projects__items').hide();
        $(tabID).show();
    });
});

// mob menu
$(() => {
    $('.mobile__burger').on('click', function() {
        if($(this).hasClass('active')) {
            $(this).removeClass('active');
            $('.mobile-menu').removeClass('active');
            $('.header').removeClass('active');
            $('body, html').css({ overflow: 'auto' });
        } else {
            $(this).addClass('active');
            $('.mobile-menu').addClass('active');
            $('.header').addClass('active');
            $('body, html').css({ overflow: 'hidden' });
        }
    });
    $('.mobile-menu__menu li a').on('click', function(e) {
        if($(this).next('ul').length) {
            e.preventDefault();
            if($(this).next('ul').hasClass('active')) {
                $(this).next('ul').removeClass('active');
            } else {
                $(this).next('ul').addClass('active')
            }
        }
    });
});

// mobile search
$(() => {
    $('.mobile__search-trigger').on('click', function() {
        $('.mobile__search').addClass('active');
    });
    $('.mobile__search button').on('click', function() {
        $('.mobile__search').removeClass('active');
    });
});

// mobile actions
$(() => {
    // mails
    $('.mobile__mail').on('click', function() {
        if($('.actions .list.mails').hasClass('active')) {
            $('.actions').removeClass('active');
            $('.actions .list.mails').removeClass('active');
        } else {
            $('.actions').addClass('active');
            $('.actions .list.mails').addClass('active');
        }
    });
    $('.actions .list.mails button').on('click', function() {
        $('.actions').removeClass('active');
        $('.actions .list.mails').removeClass('active');
    });

    // phones
    $('.mobile__phone').on('click', function() {
        if($('.actions .list.phones').hasClass('active')) {
            $('.actions').removeClass('active');
            $('.actions .list.phones').removeClass('active');
        } else {
            $('.actions').addClass('active');
            $('.actions .list.phones').addClass('active');
        }
    });
    $('.actions .list.phones button').on('click', function() {
        $('.actions').removeClass('active');
        $('.actions .list.phones').removeClass('active');
    });
});

